<template>
  <el-form
    ref="loginFormRef"
    :model="userInfo"
    :rules="loginFormRules"
    label-width="100px"
    class="loginForm sign-in-form"
  >
    <el-form-item label="用户名" prop="username">
      <el-input
        v-model="userInfo.username"
        placeholder="Enter Username..."
        clearable
      ></el-input>
    </el-form-item> 
    <el-form-item label="密码" prop="password">
      <el-input
        v-model="userInfo.password"
        type="password"
        placeholder="Enter Password..."
        show-password
        clearable
      >
      </el-input>
    </el-form-item>

    <el-form-item>
      <el-button @click="login" type="danger" class="submit-btn"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import { ElForm } from "element-plus";
import { ElFormItem } from "element-plus";
import { ElInput } from "element-plus";
import { ElButton } from "element-plus";
import axios from "axios";

export default {
  components: {
    ElFormItem,
    ElForm,
    ElInput,
    ElButton,
    axios,
  },
  data() {
    return {
      userInfo: {
        username: null,
        password: null,
      },
      loginFormRules: {
        //  验证用户名
        username: [
          {
            required: true,
            message: "请输入用户名称",
            trigger: "blur",
          },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        //验证密码
        password: [
          {
            required: true,
            message: "请输入登录密码",
            trigger: "blur",
          },
          {
            min: 3,
            max: 15,
            message: "长度在 3 到 15 个字符",
            trigger: "blur",
          },
        ],
        profile_photo: {},
      },
    };
  },
  methods: {
    // 登录请求
    login() {
      axios({
        url: "http://localhost:8888/user/login",
        method: "post",
        data: {
          username: this.userInfo.username,
          password: this.userInfo.password,
        },
        header: {
          "Content-Type": "application/json",
        },
      }).then((res) => {
        const result = res
        if (result.data.code !== 200) {
          console.log(result);
          return this.$message.error("登录失败");
        }
        this.$message.success("登录成功");
        console.log(result);
        window.sessionStorage.setItem("token", result.data.data);
        window.sessionStorage.setItem("username", this.userInfo.username);
        //window.sessionStorage.setItem("photo", result.data.obj.userInfo.imageUrl);
        const token = window.sessionStorage.getItem("token");
        console.log(token);
        this.$router.push("/");
      });
    },
  },
};
</script>
<style scoped>
/* form */
.loginForm {
  margin-right: 10%;
  margin-top: 20%;
  color: #f5f5f5;
}

.submit-btn {
  width: 100%;
}
</style>
