<template>
  <el-form
    ref="registerForm"
    :model="userInfo"
    :rules="registFormRules"
    label-width="100px"
    class="registerForm sign-up-form"
  >
    <el-form-item label="旧密码" prop="pre_password">
      <el-input
        v-model="userInfo.pre_password"
        type="pre_password"
        placeholder="Enter Old Password..."
        clearable
      ></el-input>
    </el-form-item>
    <el-form-item label="新密码" prop="password">
      <el-input
        v-model="userInfo.password"
        type="password"
        placeholder="Enter New Password..."
        clearable
      ></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="password2">
      <el-input
        v-model="userInfo.password2"
        type="password"
        placeholder="Enter New Password..."
        clearable
      ></el-input>
    </el-form-item>
    <el-form-item>
      <el-button @click="changepsw" type="danger" class="submit-btn"
        >修改</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import { ElForm } from "element-plus";
import { ElFormItem } from "element-plus";
import { ElInput } from "element-plus";
import { ElButton } from "element-plus";
import axios from 'axios';
export default {
  components: {
    ElFormItem,
    ElForm,
    ElInput,
    ElButton,
  },
  name: "Change",
  data() {
    var validatePass = (value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.userInfo.password2 !== "") {
          this.$refs.userInfo.validateField("password2");
        }
        callback();
      }
    };
    var validatePass2 = (value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.userInfo.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      userInfo: {
        pre_password: null,
        email: null,
        password: null,
        password2: null,
      },
      registFormRules: {
        pre_password: [
          {
            required: true,
            message: "请输入旧密码",
            trigger: "blur",
          },
          {
            min: 6,
            max: 40,
            message: "长度在 6 到 40 个字符",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            message: "请输入用户名称",
            trigger: "blur",
          },
          {
            min: 6,
            max: 40,
            message: "长度在 6 到 40 个字符",
            trigger: "blur",
          },
        ],
        password: [
          {
            validater: validatePass,
            trigger: "blur",
          },
        ],
        password2: [
          {
            validator: validatePass2,
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    changepsw() {
      axios({
        url: "http://localhost:8888/user/changePassword",
        method: "post",
        data: {
          username: window.sessionStorage.getItem("username"),
          pre_password: this.userInfo.pre_password,
          password: this.userInfo.password,
        },
        headers: {
          Authorization: window.sessionStorage.getItem("token"),
        },
      }).then((res) => {
        const result = res;
        if (result.data.status !== 200) {
          console.log(result);
          return this.$message.error("修改失败！" + result.data.msg);
        }
        this.$message.success("修改成功！" + result.data.msg);
        console.log(result);
      });
    },
  },
};
</script>
<style scoped>
.registerForm {
  margin-right: 10%;
}

.submit-btn {
  width: 100%;
}
</style>
