<template>
  <div style="overflow-x: hidden">
    <img class="top" src="../assets/top_logo.jpg" />
    <router-link to="/" style="color: white">
      <el-icon
        :size="50"
        style="margin-top: 16px; position: absolute; left: 50px"
      >
        <bowl />
      </el-icon>
    </router-link>
    <el-row :gutter="20" class="container">
      <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
      <el-col :span="6" class="loginregister">
        <el-row>
          <h2 v-show="model" class="h2signup">Sign Up</h2>
          <h2 v-show="!model" class="h2login">Log In</h2>
        </el-row>
        <el-row>
          <LoginForm v-show="!model"></LoginForm>
          <RegisterForm
            v-show="model"
            @justRegister="changeModel()"
          ></RegisterForm>
          <el-button @click="changeModel()" type="text" class="model_btn">{{
            modelname
          }}</el-button>
        </el-row>
      </el-col>
      <el-col :span="6"
        ><img class="login_bkg" src="../assets/login_bkg.jpg"
      /></el-col>
      <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
    </el-row>
  </div>
</template>

<script>
import LoginForm from "../components/LoginForm.vue";
import RegisterForm from "../components/RegisterForm.vue";
import { ElIcon } from "element-plus";
import { Bowl } from "@element-plus/icons";
export default {
  name: "login",
  components: {
    RegisterForm,
    LoginForm,
    ElIcon,
    Bowl,
  },
  data() {
    return {
      model: false,
      modelname: "注册",
    };
  },
  mounted() {
    console.log(this.$route.query.state);
    if (this.$route.query.state == 0) {
      this.model = true;
      this.modelname = "登录";
    } else {
      this.model = false;
      this.modelname = "注册";
    }
    console.log(this.model);
  },
  methods: {
    changeModel() {
      this.model = !this.model;
      if (this.model == true) {
        this.modelname = "登录";
      } else {
        this.modelname = "注册";
      }
    },
    reverseName() {
      if (this.model == true) return "注册";
      else return "登录";
    },
    getModelName() {
      if (this.model == true) {
        return "注册";
      } else {
        return "登录";
      }
    },
  },
};
</script>

<style scoped>
.h2signup {
  margin-left: 40%;
  color: red;
}
.h2login {
  margin-left: 40%;
  color: red;
}
.model_btn {
  margin-left: 80%;
}
.top {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 78px;
}
.container {
  margin-top: 13%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login_bkg {
  width: 400px;
  height: 380px;
}
</style>
