<template>
  <el-form
    ref="registerForm"
    :model="userInfo"
    :rules="registFormRules"
    label-width="100px"
    class="registerForm sign-up-form">
    <el-form-item>
    <el-col label="邮箱" prop="email">
      <el-input
        v-model="userInfo.email"
        placeholder="Enter Email..."
        clearable
      ></el-input>
    </el-col>
    <el-col>
      <el-button @click="sendEmail" type="danger" class="submit-btn">
        发送验证码
      </el-button>
    </el-col>
    </el-form-item>
     <el-form-item label="验证码" prop="code">
      <el-input
        v-model="userInfo.code"
        placeholder="Enter Code..."
        clearable
      ></el-input>
    </el-form-item>
    <!-- <el-form-item label="用户名" prop="username">
      <el-input
        v-model="userInfo.username"
        placeholder="Enter UserName..."
        clearable
      ></el-input>
    </el-form-item> -->
    <el-form-item label="密码" prop="password">
      <el-input
        v-model="userInfo.password"
        type="password"
        placeholder="Enter Password..."
        clearable
      ></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="password2">
      <el-input
        v-model="userInfo.password2"
        type="password"
        placeholder="Enter Password..."
        clearable
      ></el-input>
    </el-form-item>

    <el-form-item>
      <el-button @click="registry" type="danger" class="submit-btn">
        注册
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { ElForm } from "element-plus";
import { ElFormItem } from "element-plus";
import { ElInput } from "element-plus";
import { ElButton } from "element-plus";
import axios from "axios";
export default {
  components: {
    ElFormItem,
    ElForm,
    ElInput,
    ElButton,
    axios,
  },
  name: "regist",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.userInfo.password2 !== "") {
          this.$refs.userInfo.validateField("password2");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.userInfo.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      userInfo: {
        username: null,
        email: null,
        password: null,
        password2: null,
      },
      registFormRules: {
        username: [
          {
            required: true,
            message: "请输入用户名称",
            trigger: "blur",
          },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            message: "请输入用户名称",
            trigger: "blur",
          },
          {
            min: 6,
            max: 40,
            message: "长度在 6 到 40 个字符",
            trigger: "blur",
          },
        ],
        password: [
          {
            validater: validatePass,
            trigger: "blur",
          },
        ],
        password2: [
          {
            validator: validatePass2,
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    sendEmail(){
      axios({
        url: "http://localhost:8888/user/sendRegistEmail",
        method: "post",
        data: {
          email: this.userInfo.email,
        },
        header: {
          "Content-Type": "application/json",
        },
      }).then((res) => {
        const result = res;
        if (result.data.code !== 200) {
          console.log(result);
          this.$message.error("邮箱已注册或不可用");
          return;
        }
        this.$message.success("发送成功");
        console.log(result);
      });
    },
    registry() {
      axios({
        url: "http://localhost:8888/user/regist",
        method: "post",
        data: {
          //username: this.userInfo.username,
          password: this.userInfo.password,
          email: this.userInfo.email,
          code: this.userInfo.code,
        },
        header: {
          "Content-Type": "application/json",
        },
      }).then((res) => {
        const result = res;
        if (result.data.code !== 200) {
          console.log(result);
          this.$message.error("注册失败");
          return;
        }
        this.$message.success("注册成功");
        console.log(result);
        this.$router.push("/");
      });
    },
  },
};
</script>
<style scoped>
.registerForm {
  margin-right: 10%;
}

.submit-btn {
  width: 100%;
}
</style>
